
.draggable {
  width: 100px;
  height: 100px;
  background: #EF548F;
  border-radius: 50%;
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-box-shadow: 0 1px 20px rgba(0, 0, 0, 0.3);
          box-shadow: 0 1px 20px rgba(0, 0, 0, 0.3);
}



